/// <reference types="core-js" />
/// <reference path="./typings.custom.d.ts" />

import 'bootstrap/scss/bootstrap.scss';
import 'angular2-calendar/scss/angular2-calendar.scss';
import 'core-js/es7/reflect';
import 'zone.js/dist/zone';
import 'rxjs/Observable';
import {NgModule, enableProdMode} from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';
import {RouterModule} from '@angular/router';
import {SubscribedShows} from './modules/subscribed';
import {SharedModule} from './modules/shared';
import {AppComponent} from './app.component';

declare var ENV: string;
if (ENV === 'production') {
  enableProdMode();
}

@NgModule({
  declarations: [
    AppComponent,
    SubscribedShows
  ],
  imports: [
    BrowserModule,
    SharedModule.forRoot(),
    RouterModule.forRoot([
      {path: 'subscribed', component: SubscribedShows},
      {path: 'episodes/:id', loadChildren: () => System.import('../../aot/src/app/modules/+episodes/index.ngfactory')  .then(function(module) {    return module['EpisodesModuleNgFactory'];  })},
      {path: 'search', loadChildren: () => System.import('../../aot/src/app/modules/+search/index.ngfactory')  .then(function(module) {    return module['SearchModuleNgFactory'];  })},
      {path: 'schedule', loadChildren: () => System.import('../../aot/src/app/modules/+schedule/index.ngfactory')  .then(function(module) {    return module['ScheduleModuleNgFactory'];  })},
      {path: '**', redirectTo: 'subscribed'}
    ], {useHash: true})
  ],
  bootstrap: [
    AppComponent
  ]
})
export class AppModule {}


